<!-- 해당 가게의 메뉴 리스트 카드 -->
<template>
   <div class="card" v-for="(menu,index) in menus" :key="index" @click="show(menus[index].fnum)">
        <div class="card-body">
            <!-- 분류 넣고 싶음 -->
          <h5 class="card-title">{{ menu.fname }}</h5>
          <p class="card-text">{{ menu.fprice }} 원</p>
          <!-- <p class="card-text"><small class="text-body-secondary">Last updated 3 mins ago</small></p> -->
        </div>
      </div>
</template>
<script>
export default {
    props:["menus"],
    name: 'DJ_C_MenuCard',
    methods:{
        show(fnum){
            this.$router.push({ path: '/item/itemView', query: { f: fnum } });
        }
    }
}
</script>